'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { payrollAPI } from '@/lib/api';
import { PayrollRecord } from '@/lib/types';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { Badge } from '@/components/ui/badge';
import { useToast } from '@/hooks/use-toast';
import { DollarSign } from 'lucide-react';

export default function PayrollPage() {
  const { user } = useAuth();
  const { toast } = useToast();
  const [payrolls, setPayrolls] = useState<PayrollRecord[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchPayrolls();
  }, []);

  const fetchPayrolls = async () => {
    try {
      const response = await payrollAPI.getAll();
      setPayrolls(response.data.data || []);
    } catch (error) {
      toast({
        title: 'Error',
        description: 'Failed to fetch payroll records',
        variant: 'destructive',
      });
    } finally {
      setLoading(false);
    }
  };

  const handleBulkGenerate = async () => {
    const now = new Date();
    try {
      await payrollAPI.bulkGenerate({
        month: now.getMonth() + 1,
        year: now.getFullYear(),
      });
      toast({
        title: 'Success',
        description: 'Payroll generated for all employees',
      });
      fetchPayrolls();
    } catch (error: any) {
      toast({
        title: 'Error',
        description: error.response?.data?.message || 'Failed to generate payroll',
        variant: 'destructive',
      });
    }
  };

  const getStatusBadge = (status: string) => {
    const variants: any = {
      pending: 'default',
      processed: 'secondary',
      paid: 'success',
    };
    return <Badge variant={variants[status] || 'default'}>{status}</Badge>;
  };

  const canManagePayroll = ['hr_staff', 'admin'].includes(user?.role || '');

  if (loading) {
    return <div className="flex items-center justify-center h-96">Loading...</div>;
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold">Payroll Management</h1>
          <p className="text-muted-foreground">
            View and manage employee payroll
          </p>
        </div>
        {canManagePayroll && (
          <Button onClick={handleBulkGenerate}>
            <DollarSign className="mr-2 h-4 w-4" />
            Generate Monthly Payroll
          </Button>
        )}
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Payroll Records</CardTitle>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                {user?.role !== 'employee' && <TableHead>Employee</TableHead>}
                <TableHead>Period</TableHead>
                <TableHead>Base Salary</TableHead>
                <TableHead>Bonuses</TableHead>
                <TableHead>Deductions</TableHead>
                <TableHead>Net Salary</TableHead>
                <TableHead>Status</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {payrolls.length === 0 ? (
                <TableRow>
                  <TableCell
                    colSpan={user?.role !== 'employee' ? 7 : 6}
                    className="text-center"
                  >
                    No payroll records found
                  </TableCell>
                </TableRow>
              ) : (
                payrolls.map((payroll) => (
                  <TableRow key={payroll._id}>
                    {user?.role !== 'employee' && (
                      <TableCell>
                        {typeof payroll.employeeId === 'object'
                          ? payroll.employeeId.name
                          : 'Unknown'}
                      </TableCell>
                    )}
                    <TableCell>
                      {new Date(payroll.year, payroll.month - 1).toLocaleDateString(
                        'en-US',
                        { month: 'long', year: 'numeric' }
                      )}
                    </TableCell>
                    <TableCell>${payroll.baseSalary.toFixed(2)}</TableCell>
                    <TableCell className="text-green-600">
                      +${payroll.bonuses.toFixed(2)}
                    </TableCell>
                    <TableCell className="text-red-600">
                      -${payroll.deductions.toFixed(2)}
                    </TableCell>
                    <TableCell className="font-semibold">
                      ${payroll.netSalary.toFixed(2)}
                    </TableCell>
                    <TableCell>{getStatusBadge(payroll.status)}</TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  );
}
